import React from "react"
import { graphql } from "gatsby"
import { Translation } from "react-i18next"
import i18n from "../lib/i18n"

import Layout from "../components/layout"
import SEO from "../components/seo"
import HeroHalf from "../components/heros/hero-half"

export const query = graphql`
  query {
    wpHero(id: { eq: "cG9zdDoyNjE=" }) {
      id
      title
      heroFields {
        en {
          headline
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
        es {
          headline
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
      }
    }
  }
`

const HVACFinancing = ({ data }) => {
  const hero = data.wpHero.heroFields

  return (
    <Layout>
      {i18n.language === "en" ? (
              <SEO title="HVAC Financing | We'll Help You Finance your AC or Furnace" />
            ) : (
              <SEO title="Financiamiento de HVAC | Te Ayudamos a Financiar Tu Aire Acondicionado o Calenton" />
            )}
      <Translation>
        {t => (
          <HeroHalf
            headline={`${hero[i18n.language].headline}`}
            smallcta={``}
            subheadline={`${hero[i18n.language].subheadline}`}
            body={`${hero[i18n.language].body}`}
            image={`${hero[i18n.language].image.sourceUrl}`}
            button2Text={`${hero[i18n.language].buttontext}`}
            button2Link={`${hero[i18n.language].buttonlink}`}
          />
        )}
      </Translation>
    </Layout>
  )
}

export default HVACFinancing
